export default {
	components: {

	},
	data() {
		return {
			inputVal: '',
			historyList: [],
			hotList: ['北京大学','清华大学','上海大学','复旦大学','香港大学','华中科技大学'],
		}
	},
	onLoad() {
		this.getSearchHistory()
	},
	methods: {
		//清除输入框
		cancelClick() {
			this.inputVal = ''
		},
		//搜索点击
		searchClick() {
			this.saveSearchHistory()
			uni.navigateTo({
				url:'/pages/search/searchResult'
			})
		
			if(this.inputVal){
				let value = this.inputVal.trim()
				uni.navigateTo({
					url:`/pages/search/searchResult?inputVal=${value}`
				})
				this.inputVal = ''
			}
		},
		//保存搜索历史记录
		saveSearchHistory() {
			let value = this.inputVal.trim()
			let list = this.historyList
			if (this.inputVal && value) {
				if (list.length > 0) {
					console.log(list, 'list')
					let index = list.findIndex((item) => item === value)
					if (index !== -1) list.splice(index, 1)
				}
				list.unshift(value)
				if (list.length > 10) list.splice(10)
				uni.setStorage({
					key: 'historyList',
					data: JSON.stringify(list)
				})
			}
		},
		//获取搜索历史记录
		getSearchHistory() {
			uni.getStorage({
				key: 'historyList',
				success: res => {
					this.historyList = JSON.parse(res.data)
				}
			})
		},
		//清空历史记录
		deleteHistoryList() {
			uni.removeStorage({
				key: 'historyList',
				success: res => {
					this.historyList = []
				}
			});
		}
	}
}
